					<el-row type="flex" justify="center">
						<el-col :span="2">ÂõæÊ†á</el-col>
						<el-col :span="4">ÂêçÁß∞</el-col>
						<el-col :span="6">ÈìæÊé•</el-col>
						<el-col :span="6">‰ªãÁªç</el-col>
						<el-col :span="3">Êñá‰ª∂Â§π</el-col>
						<el-col :span="3">Êìç‰Ωú</el-col>
					</el-row>
