						<el-col :span="4"><el-input type="text" v-model="linkform.name" 	minlength="0" maxlength="8"  placeholder="0-8Â≠ó/ËøáÈïø‰∏çÂ•ΩÁúã"></el-input></el-col>
						<el-col :span="6"><el-input type="text" v-model="linkform.url"		minlength="0" maxlength="50" placeholder="httpÂºÄÂ§¥" ></el-input></el-col>
						<el-col :span="6"><el-input type="text" v-model="linkform.info"		minlength="0" maxlength="30" placeholder="Èº†Ê†áÊîæ‰∏äÊó∂ÁöÑÊèêÁ§∫ËØ≠(ÂèØ‰∏∫Á©∫)"></el-input></el-col>
