<script>

import * as FolderAPI from '@/api/folder/'
import * as LinkAPI from '@/api/link/'

export default {
	props:["userID"],
	data() {
		return {
			loading: false,
			FolderID: "",
			uid: 0,
			Folders: [],
			links: [],
			linkform: {
				id: "",
				fid: "",
				icon: "",
				name: "",
				url: "",
				info: "",
			}
		}
	},
	methods: {
		getFolder(){
			this.uid = this.userID,
			FolderAPI.getFoldersbyID(this.uid).then((res) =>{
				this.Folders = res.data
			})
		},
		getLinksin(fid){
			// ËøôÊ†∑Âè™Âú®ÊäòÈ°µÊâìÂºÄÊó∂ÊâßË°å
			this.loading = true;
			setTimeout(() => {
				this.loading = false;
			}, 500);
			if(fid){
				LinkAPI.getLinksbyFolderID(fid).then((res) =>{
					this.links = res.data
				})
			}
			else{
				// ÊäòÈ°µÂÖ≥Èó≠
			}
		},
		createLink(fid){
			this.linkform.fid = fid
			LinkAPI.createLink(this.linkform).then((res) => {
				if (res.code > 0) {
					this.$notify.error({
					title: "Ê∑ªÂä†Â§±Ë¥•",
					message: res.msg
					});
				} else {
					this.$notify({
						title: "Ê∑ªÂä†ÊàêÂäü!",
						type: "success",
						message: "Âà∑Êñ∞ÂàóË°®",
						duration: "800"
					});
					this.linkform = {id: "",fid: "",icon: "",name: "",url: "",info: "",}
					this.getLinksin(fid)
				}
				})
				.catch(error => {
				this.$notify.error({
					title: "ÈîôËØØ ËØ∑Ê£ÄÊü•",
					message: error
				});
			});
		},
		updateLink(fid,link){
			//‰º†ÂÖ•folderID‰ªÖ‰ªÖÊòØ‰∏∫‰∫ÜÊõ¥Êñ∞ÂêéÂà∑Êñ∞ÂàóË°®
			var form = {
				id: link.id,
				fid: link.fid,
				icon: link.icon,
				name: link.name,
				url: link.url,
				info: link.info
			}
			LinkAPI.updateLink(form).then((res) => {
				if (res.code > 0) {
					this.$notify.error({
					title: "Êõ¥Êñ∞Â§±Ë¥•",
					message: res.msg
					});
				} else {
					this.getLinksin(fid)
					this.$notify({
						title: "Êõ¥Êñ∞ÊàêÂäü!",
						type: "success",
						duration: "800"
					});
				}
				})
				.catch(error => {
				this.$notify.error({
					title: "ÈîôËØØ ËØ∑Ê£ÄÊü•",
					message: error
				});
			});
		},
		deleteLink(fid,link){
			this.linkform.fid = fid
			var form = {
				data:{id: link.id}
			}
			LinkAPI.deleteLink(form).then((res) => {
				if (res.code > 0) {
					this.$notify.error({
					title: "Âà†Èô§Â§±Ë¥•",
					message: res.msg
					});
				} else {
					this.$notify({
					title: "Âà†Èô§ÊàêÂäü!",
					type: "success",
					duration: "800"
					});
					this.getLinksin(fid)
				}
				})
				.catch(error => {
				this.$notify.error({
					title: "ÈîôËØØ ËØ∑Ê£ÄÊü•",
					message: error
				});
			});
		}
	},
	components:{

	},
	// beforeMount() {
	// 	this.getUser()
	// },
	watch: {
        userID: function() {
            this.uid = this.userID,
            this.getFolder()
        },
    }
}

</script>

