	},
	methods: {
		getFolder(){
			this.uid = this.userID,
			FolderAPI.getFoldersbyID(this.uid).then((res) =>{
				this.Folders = res.data
			})
		},
		createFolder(){
			FolderAPI.createFolder(this.Folderform).then((res) => {
				if (res.code > 0) {
					this.$notify.error({
					title: "Ê∑ªÂä†Â§±Ë¥•",
